import React, { useEffect, useState } from "react";
import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getInsurance,
  insuranceEdit,
  insuranceDelete,
  isModelVisible,
  SetInsuranceParams,
} from "../../../../store/Masters/Insurance/insuranceSlice";
import AddInsurance from "./addInsurance";

function Insurance(props) {
  useEffect(() => {
    props.getInsurance({ page: 1, search: "", page_size: 10 });
  }, []);
  const { Search } = Input;
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "10%",
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "40%",
    },
    {
      title: "Insurance Name",
      dataIndex: "name",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "50%",
    },
    {
      title: "Actions",
      key: "action",
      sorter: false,
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_insurancecompany") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.insuranceEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_insurancecompany") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.insuranceDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.insurancelist.map((row, key) => ({
    ...row,
    index:
      (props.insuranceParams.page - 1) * props.insuranceParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetInsuranceParams({
      ...props.insuranceParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getInsurance({
      ...props.insuranceParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetInsuranceParams({ ...props.insuranceParams, search: val });
    props.getInsurance({ ...props.insuranceParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Insurance Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_insurancecompany") !== -1 &&
          "Add Insurance"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24} xs={24} sm={24} md={16}  className="hp-mb-16">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getInsurance({
                      ...props.insuranceParams,
                      page: 1,
                      search: "",
                    });
                    props.SetInsuranceParams({
                      ...props.insuranceParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.insuranceParams?.page}
                pageSize={props.insuranceParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddInsurance />
    </Row>
  );
}

function mapStateToProps({ insurance, system }) {
  return {
    insurancelist: insurance.insurancelist,
    noofpages: insurance.noofpages,
    listCount: insurance.listCount,
    insuranceParams: insurance.insuranceParams,
    loading: insurance.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getInsurance,
  insuranceEdit,
  insuranceDelete,
  isModelVisible,
  SetInsuranceParams,
})(Insurance);
